import { call, put, takeLatest } from 'redux-saga/effects';
import {getSchoolListRequest, getSchoolListSuccess, getSchoolListFailure, getMemberSuccess,getMemberFailure} from '../actions/school';
import {fetchSchoolList,registerSchoolAPI,fetchMemberListService, registerMultipleSchoolAPI,deleteUserAPI} from '../services';
import { reducerTypes } from '../constants/index';
import { addSysMessage } from '../../views/pages/notifications/system/sysMessageAction'

const {schoolConstants} = reducerTypes;

export const SYS_MESSAGE_TYPE_ERROR = 'error';
export const SYS_MESSAGE_TYPE_SUCCESS = 'success';
export const SYS_MESSAGE_TYPE_INFO = 'info';

export  function*  fetchSchoolUsersList(action) {


    const { payload } = action;
    const {response} =  yield  call(fetchSchoolList, payload);
    if (response) {

        yield  put(getSchoolListSuccess(response));
    } else {
      yield put(getSchoolListFailure());
    }
  }

  export  function* fetchMembersList(action) {
    const { payload } = action;
    //const response = []
    const {response} =  yield call(fetchMemberListService, payload);
    if (response) {

        yield put(getMemberSuccess(response));
    } else {
      yield put(getMemberFailure());
    }
  }



export  function*  registerSchool(action) {
    const { payload } = action;
    
        const { response, error } =  yield  call(registerSchoolAPI, payload.registerData);
        // success trigger here

        if(response) {
          yield put(addSysMessage(localStorage.getItem('UserType') === 'false' ? 'registered member succesfully' : "registered school succesfully", SYS_MESSAGE_TYPE_SUCCESS))
          if(payload.navigateFun) {
            payload.navigateFun()
          }
        } else if(error) {
          yield put(addSysMessage('please fill all required field', SYS_MESSAGE_TYPE_ERROR))
        } else {
          yield put(addSysMessage('server connection interrupted', SYS_MESSAGE_TYPE_ERROR))
        }

}

export  function*  registerMultipleSchool(action) {
  const { payload } = action;
 
      const { response, error } =  yield  call(registerMultipleSchoolAPI, payload);
      // success trigger here
      if(response) {
        yield put(addSysMessage(localStorage.getItem('UserType') === 'false' ? 'registered member succesfully' : "registered school succesfully", SYS_MESSAGE_TYPE_SUCCESS))
      }


    yield put(addSysMessage('Something went wrong', SYS_MESSAGE_TYPE_SUCCESS))
  }


export  function*  deleteUser(action) {
    const { payload } = action;

    const { response, error } =  yield  call(deleteUserAPI, payload);
    if(response) {
      yield put(addSysMessage('deactived successfully', SYS_MESSAGE_TYPE_SUCCESS))
    } else if(error) {
      if(error.error) {
        yield put(addSysMessage(error.error.message, SYS_MESSAGE_TYPE_SUCCESS))
      }
    }

}

  export function *schoolWatcher() {
    yield takeLatest(
      schoolConstants.GET_MEMBER_LIST_REQUEST,
      fetchMembersList
  );
    yield takeLatest(
        schoolConstants.GET_SCHOOL_LIST_REQUEST,
        fetchSchoolUsersList
    );
      yield takeLatest(
          schoolConstants.REGISTER_SCHOOL,
          registerSchool
      );
      yield takeLatest(
        schoolConstants.REGISTER_MULTIPLE_SCHOOL_REQUEST,
        registerMultipleSchool
    );

      yield takeLatest(
          schoolConstants.DELETE_USER_REQUEST,
          deleteUser
      )
}


