import React from 'react';
import { Provider } from 'react-redux'
import './App.css';
import {AllRoutes} from "../src/routes/newRoutes";
import store,{history} from './store'
import { ConnectedRouter } from 'connected-react-router'
import { Route } from 'react-router-dom'
import SystemMessage from './views/pages/notifications/system/components/SystemMessage'

import LoginPage from "../src/views/pages/login/login"
import LoginLayout from "../src/views/layouts/loginLayout";

function App() {
  return (
    <div className="App">
      <header className="App-header">
          <Provider store={store}>
          <SystemMessage/>
              <ConnectedRouter history={history}>
              <Route path="/" exact component={() =><LoginLayout><LoginPage/></LoginLayout>}/>
              <AllRoutes/>
              </ConnectedRouter>
          </Provider>
      </header>
    </div>
  );
}

export default App;
