import { call,put, takeLatest } from 'redux-saga/effects'
import {loginPending,loginSuccess,loginError} from "../actions/login";
import {setAuthToken} from '../../utils/Auth'
import {loginAPI} from "../services";
import { reducerTypes } from '../constants'
import { showLoading, hideLoading } from 'react-redux-loading-bar'
import { addSysMessage } from '../../views/pages/notifications/system/sysMessageAction'


const { login } = reducerTypes;

export const SYS_MESSAGE_TYPE_ERROR = 'error';
export const SYS_MESSAGE_TYPE_SUCCESS = 'success';
export const SYS_MESSAGE_TYPE_INFO = 'info';


function *loginSaga(action) {
       // yield put(showLoading());
       // yield put(loginPending(true));
        const res = yield call(
            loginAPI,
            action.payload.loginData
        );


        if(res.response){
           // yield put(hideLoading());
            yield put(loginSuccess(res));
            yield setAuthToken(res.response.token);
            yield put(addSysMessage('login succesfully', SYS_MESSAGE_TYPE_SUCCESS))
            //window.location.href = "http://localhost:3005/listschool"
            if(res.response.is_staff) {
                action.payload.props.history.push('listschool')
            } 
            if(!res.response.is_staff) {
                action.payload.props.history.push('userdetails')
            }
            localStorage.setItem('UserType', res.response.is_staff);
            localStorage.setItem('schoolId', res.response.school.id);
            localStorage.setItem('SchoolName', res.response.school.name)
        } else if(res.error) {
            yield put(loginError(res.error));
        }

    else{
        yield put(loginError(true));
        yield put(loginPending(false));
        yield put(hideLoading())

    }

}




export function *authWatcher() {

    yield takeLatest(
        login.LOGIN_PENDING,
        loginSaga
    )

}