import { reducerTypes } from '../constants/index'

const  inititalState= {
        success: false,
        error: "",
        pending: false,
        isAuthorized:false,
        isStaff: false,
        profile: {},
        school: {}
}

 const { login } = reducerTypes,

 Login = (state = inititalState, action) => {

    switch (action.type) {

        case login.LOGIN:
            return state;
        case login.LOGIN_PENDING:
            return state = {
                ...state,
                    pending: action.payload

            };
        case login.LOGIN_SUCCESS:
            return state = {
                    ...state,
                    success: true,
                    error: "",
                    isAuthorized: true,
                    isStaff:action.payload.response.is_staff,
                    profile:action.payload.response,
                    school:action.payload.response.school

            };
        case login.LOGIN_ERROR:
            return state = {
                    ...state,
                    error: action.payload.error.message

            };
        default:
            return state
    }

};

export default Login;


