import React, {Component} from "react";
import {connect } from 'react-redux';
import TopBreadCrumb from "../../partials/breadcrumb";
import schoolListIcon from "../../../assets/School_list_ic.png";
import {Col, Row, Popover, PopoverTitle, PopoverContent,Button, Overlay} from "react-bootstrap";
import UserModal from "../modals/userModal";
import UserDeleteModal from "../modals/UserDeleteModal";
import EditModal from '../modals/userModal';
import DeleteModal from './delete'
import UserDataTable from './StudentList';
import {getMemberRequest} from '../../../store/actions/school'
import {withRouter} from 'react-router-dom'
import InfoIcon from '@material-ui/icons/Info';
import ToolTip from 'react-portal-tooltip'
import _isEqual from "lodash/isEqual"
//import EditModal from '../modals/userModal';
import './dashboard.css'


class UserDetails extends Component {

    constructor() {
        super();
        this.state={
            userDetails:{},
            show: false
        }
    }
    componentDidMount() {
        this.props.getMemberRequest({id: this.props.loginData.id})
    }

    componentDidUpdate(prevProps, prevState) {
        if(!_isEqual(prevProps.memberList, this.props.memberList)) {
            this.props.getMemberRequest({id: this.props.loginData.id})
        }
    }
    
   

    render () {
        let style = {
            style: {
              background: 'rgb(53, 168, 252)',
              padding: 20,
              //boxShadow: '5px 5px 3px rgba(0,0,0,.5)',
              left:'1180px !important',
              top: '295px'
            },
            arrowStyle: {
              color: 'rgb(53, 168, 252)',
              borderColor: false
            }
          }
        const memberKeys = [
            { key: 'name', columnName: 'User Name', type: 'admin_username', form: false, required: false, visible: true, value: true },
            { key: 'address', columnName: 'Address', type: 'address', form: false, required: false, visible: true, value: true },
            { key: 'role', columnName: 'Role Type', type: 'role', form: false, required: false, visible: true, value: true },
            { key: 'email', columnName: 'User Email', label: "User Email", render: (value,record,index,newRecord) => {
                return(
                    <div>{newRecord.user.email}</div>
                )
            },visible: true, form: false },
            { key: 'phone', columnName: 'User Phone', label: "User Phone", render: (value,record,index,newRecord) => {
                return(
                    <div>{newRecord.user.phone_number}</div>
                )
            },visible: true, form: false },
            { key: 'driver_contact_no', columnName: 'Driver Contact No', type: 'driver_contact_no', form: false, required: false, visible: true, value: true },
            { key: 'admission_no', columnName: 'Admission No', type: 'admission_no', form: false, required: false, visible: true, value: true },
            { key: 'teacher_id', columnName: 'Teacher Id', type: 'teacher_id', form: false, required: false, visible: true, value: true },
            {key: 'other', columnName: 'Other', label: 'other', render: (value, record, index, newRecord) => {
                return(
                    <div className="popovercontainer" id={`text_${newRecord.id}`}>
                        <InfoIcon 
                            onClick={(event) => 
                                {
                                    this.setState({show: !this.state.show})
                                }
                            }
                            //onMouseLeave = {() => this.setState({show: !this.state.show}) }
                            />
                              {
                               record.role && record.role.toLowerCase() !== 'teacher' ?
                        <ToolTip active={this.state.show}  position="bottom" arrow="top" style={style} parent={`#text_${record.id}`}>
                          
                    <div>
                        
                    <div style={{display: "flex",  justifyContent: "space-between"}}>
                        <div className="color">Parent Name</div>
                        <div className="color">Bus No</div>
                        <div className="color">Standard</div>
                    </div>
                    <div style={{display: "flex", justifyContent: "space-between"}}>
                        <div className="color">{record.parent_name}</div>
                        <div className="color">{record.bus_no}</div>
                        <div className="color">{record.standard}</div>
                    </div>
            
                    </div> 
                </ToolTip> : null
            }
                        {/* <Overlay
                            show={this.state.show}
                            placement="bottom"
                           // container={ref.current}
                            containerPadding={20}
                        >
                            <Popover id="popover-contained" className="testttt">
                                <div style={{display: "flex"}}>
                                    <PopoverTitle>Parent Name</PopoverTitle>
                                    <PopoverTitle>Bus No</PopoverTitle>
                                    <PopoverTitle>Roll Number</PopoverTitle>
                                </div>
                            <Popover.Content>
                            <div style={{display: "flex", justifyContent: "space-between"}}>
                               <span> Parent Name </span>
                               <span> Bus No </span>
                               <span> Roll Number </span>
                            </div>
                            </Popover.Content>
                            </Popover>
                        </Overlay> */}
    
                    </div>
                )
            },visible: true, form: false },
            
            
            
            {
                key: 'action', columnName: 'Actions', label: 'Actions', render: (value, record) => { 
                    return(
                    <div style={{display: "flex"}}>
            <UserModal userDetails={record} />
            <DeleteModal deleteRecord={record} title="Deactivate Member" subHeading="Are you sure want to deactivate selected member?"/>
        </div>
                    
                )},visible: true, form: false
            }
        ]
    return (
        <>
            <Row className={'mar_pad_0'}>
                <Col className={'mar_pad_0'}>
                    <TopBreadCrumb name={"Member's List"} icon={schoolListIcon} isSearch={true} bread={this.props}/>
                </Col>
            </Row>
            <Row className={'mar_pad_0'}>
                <Col className={'mar_pad_0'}>
                    <UserDataTable fields = {memberKeys} data={this.props.memberList}/>
                    {/* <button onClick={()=>{this.setState({showUserModal:true,modalType:'new'})}}>Click to open add user modal</button>
                    <button onClick={()=>{this.setState({showUserModal:true,modalType:'edit'})}}>Click to open edit user modal</button>
                    {this.state.showUserModal ? <UserModal show={this.state.showUserModal} onClose={this.handleModalClose} userDetails={this.state.userDetails}  type={this.state.modalType} /> : "" } */}

                </Col>
            </Row>
        </>
    )
}

}

const mapStateToProps = (state) => {
    return {
        memberList: state.School.memberList.list,
        loginData: state.Login.school

    }
}


export default withRouter(connect(mapStateToProps, {getMemberRequest})(UserDetails))
