import React from 'react'
import { connect } from 'react-redux'
import { BrowserRouter, Route, Redirect, Switch } from 'react-router-dom'

import dashboardPage from "../views/pages/dashboard/dashboard";
import RegisterSchoolModal from "../views/pages/dashboard/registerschool";
import UserDetailsPage from "../views/pages/dashboard/userDetails";
import settings from "../views/pages/settings/settings";
import loginPage from "../views/pages/login/login";
import LoginLayout from "../views/layouts/loginLayout";
import MainLayout from "../views/layouts/MainLayout";
import UserModal from '../views/pages/modals/userModal'
import userModal from '../views/pages/dashboard/registerUser';

const allRouters = [
    {
        path: '/listschool',
        name: 'Schools List',
        component: dashboardPage,
        exact: true,
    },
    {
        path: `/register`,
        name: 'Register New School',
        component: RegisterSchoolModal,
        exact: true,
    },
    {
        path: `/user_register`,
        name: 'Register New User',
        component: userModal,
        exact: true,
    },
    {
        path: '/login',
        name: 'login',
        component: loginPage,
        exact: true,
    },

    {
        path: `/settings`,
        name: 'Settings',
        component: settings,
        exact: true,
    },
    {
        path: `/userdetails`,
        name: 'User Details',
        component: UserDetailsPage,
        exact: true,
    },

];

export function AllRoutes() {
    const type = localStorage.getItem('UserType');
const parseType = JSON.parse(type)

    return(
        <>
        <Switch>
             {allRouters.map(({ name, path, component: RouteComponent }) => {
                 return(
          <Route
            key={name}
            exact
            path={path}
            render={routeProps => path === "/login"  ?  
            <LoginLayout> 
                <RouteComponent  {...routeProps} />
            </LoginLayout> : <MainLayout><RouteComponent {...routeProps}/></MainLayout>
            }
          />
        )})}
        </Switch>
        </>
    )


}