import request from '../../utils/request';
import qs from "qs"

export const fetchSchoolList = data => {
    return request({
      url: 'school/',
      method: 'get',
    })
      .then(response => response.data)
      .catch(({ data }) => ({ error: data }));
  };

  export const fetchMemberListService = data => {
    return request({
      url: `schooluser/?school=${localStorage.getItem('schoolId')}`,
      method: 'get',
    })
      .then(response => response.data)
      .catch(({ data }) => ({ error: data }));
  };

export const loginAPI = data => {
    return request({
        url: 'login/',
        method: 'post',
        data,
        headers: {
            'Content-Type': 'multipart/form-data'
        }
    }).then(response => { return response.data})
    .catch(({ data }) => ({ error: data }));

};

export const registerSchoolAPI = data => {
    const {mode, ...rest} = data;
    return request({
        url: localStorage.getItem('UserType') === "true" ? 'school/' : 'schooluser/',
        method: mode === "edit" ? 'put' : 'post',
        data:rest,
        headers: {
            'Content-Type': 'application/json'
        }
    }).then(response => response.data)
    .catch(({ data }) => ({ error: data }));
};

export const registerMultipleSchoolAPI = data => {
  return request({
      url: localStorage.getItem('UserType') === "true" ? 'school/' : 'schooluser/',
      method: 'post',
      data,
      headers: {
          'Content-Type': 'application/json'
      }
  }).then(response => response.data)
  .catch(({ data }) => ({ error: data }));
};

export const deleteUserAPI = data => {
    return request({
        url: 'user/deactivate/',
        method: 'post',
        data,
        headers: {
            'Content-Type': 'application/json'
        }
    }).then(response => response.data)
    .catch(({ data }) => ({ error: data }));
};